import React, { useState } from 'react';
import { FormControl, FormLabel, FormGroup, FormControlLabel, TextField, Button } from '@material-ui/core';


const HomePage = () => {
    const [state, setState] = useState({
        businessType: '',
        zipCode: ''
    });

    const handleChange = name => event => {
        setState({ ...state, [name]: event.target.value })
    }

    return (
        <FormControl component="fieldset">
            <FormLabel component="legend">Enter a Business Type and Zip Code to Search</FormLabel>
            <FormGroup>
                <FormControlLabel
                    control={<TextField onChange={handleChange('businessType')} value="businessType" />}
                    label="Business Type"
                />
                <FormControlLabel
                    control={<TextField onChange={handleChange('zipCode')} value="zipCode" />}
                    label="Zip Code"
                />
                <Button>
                    Submit
                </Button>
            </FormGroup>
        </FormControl>
    )
}

export default HomePage;